<template>
  <div v-if="telefon">
    <AppHeader :title="telefon.naziv" />
    <div class="info">
      <h3>Opis</h3>
      <p>{{ telefon.opis }}</p>

      <h3>Cena</h3>
      <p>{{ telefon.cena }}</p>

      <h3>Brend</h3>
      <p>{{ computedBrendNaziv }}</p>

      <h3>Memorija</h3>
      <p>{{ computedMemorijaVelicina }}</p>

      <!-- Dodato dugme za kupovinu -->
      <button @click="kupiTelefon">Kupi</button>
    </div>
  </div>
</template>

<script>
import AppHeader from '@/components/AppHeader.vue';
import { mapActions } from 'vuex';

export default {
  name: 'TelefonView',
  components: {
    AppHeader
  },
  data() {
    return {
      telefon: null,
    };
  },
  methods: {
    ...mapActions(['getTelefon', 'getBrendovi', 'getMemorija', 'kupiTelefon']),
    async fetchBrendNaziv() {
      if (this.telefon && this.telefon.brend_id) {
        await this.getBrendovi();
        const brend = this.$store.state.brendovi.find((b) => b.id === this.telefon.brend_id);
        return brend ? brend.naziv : 'nepoznat';
      }
      return '';
    },

    async fetchMemorijaVelicina() {
      if (this.telefon && this.telefon.memorija_id) {
        await this.getMemorija();
        const memorija = this.$store.state.memorije.find((b) => b.id === this.telefon.memorija_id);
        return memorija ? memorija.velicina : 'nepoznat';
      }
      return '';
    },
async kupiTelefon() {
      // Implementirajte logiku za slanje zahteva za kreiranje nove stavke narudžbine
      try {
        const response = await fetch('http://localhost:9000/stavkaNarudzbine', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            telefon: this.telefon,
            // Dodajte druge potrebne informacije vezane za narudžbinu
          }),
        });

        if (response.ok) {
          // Ukoliko je zahtev uspešan, možete izvršiti dodatne radnje
          console.log('Telefon uspešno kupljen!');
        } else {
          console.error('Greška prilikom kupovine telefona:', response.statusText);
        }
      } catch (error) {
        console.error('Greška prilikom slanja zahteva:', error);
      }
    },
  },
  computed: {
    computedBrendNaziv() {
      if (this.telefon && this.telefon.brend_id) {
        const brend = this.$store.state.brendovi.find((b) => b.id === this.telefon.brend_id);
        return brend ? brend.naziv : 'nepoznat';
      }
      return '';
    },

    computedMemorijaVelicina() {
      if (this.telefon && this.telefon.memorija_id) {
        const memorija = this.$store.state.memorije.find((b) => b.id === this.telefon.memorija_id);
        return memorija ? memorija.velicina : 'nepoznat';
      }
      return '';
    },
  },
  mounted() {
    this.getTelefon(this.$route.params.id).then(async (res) => {
      this.telefon = res;
      await this.fetchBrendNaziv();
      await this.fetchMemorijaVelicina();
    });
  }
};
</script>

<style scoped>
.torba {
  text-align: left;
  background: #f44336;
  color: #fff;
}
.info {
  overflow: auto;
}
</style>