import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    brendovi: [],
    telefoni: [],
    sviTelefoniIDs: [],
    memorije: []
  },
  getters: {

  },
  mutations: {
    addBrendovi(state, brendovi) {
      state.brendovi = brendovi;
    },
    addMemorije(state, memorije) {
      state.memorije = memorije;
    },
    addTelefon(state, telefon) {
      if (telefon && telefon.id) {
        Vue.set(state.telefoni, telefon.id, telefon);
      } else {
        console.error("Nevažeći objekat telefona ili nedostaje svojstvo 'id'.");
      }
    },
    addSviTelefoniIDs(state, niz) {
      state.sviTelefoniIDs = niz;
    },
  },
  actions: {
    async getBrendovi({ commit }) {
      console.log('Fetching brendovi...');

      try {
        const response = await fetch('http://localhost:9000/brend');
        const data = await response.json();
        console.log('Fetched brendovi:', data);
        commit('addBrendovi', data);
      } catch (error) {
        console.error('Error fetching brendovi:', error);
      }
    },

    async getMemorija({ commit }) {
      console.log('Fetching memorije...');
      try {
        const response = await fetch('http://localhost:9000/memorija');
        const data = await response.json();
        console.log('Fetched memorije:', data);
        commit('addMemorije', data);
      } catch (error) {
        console.error('Error fetching memorije:', error);
      }
    },

    async getTelefon({ commit, state }, telefonID) {
      return new Promise((resolve) => {
        if (state.telefoni[telefonID]) {
          resolve(state.telefoni[telefonID]);
        } else {
          fetch(`http://localhost:9000/telefon/${telefonID}`)
            .then(res => res.json())
            .then(data => {
              commit('addTelefon', data);
              resolve(data);
            })
            .catch(error => {
              console.error('Error fetching telefon:', error);
            });
        }
      });
    },
    async fetchSviTelefoni({ commit }) {
      fetch("http://localhost:9000/telefon")
        .then(res => res.json())
        .then(data => commit('addSviTelefoniIDs', data))
        .catch(error => {
          console.error('Error fetching svi telefoni:', error);
        });
    },
  },
  modules: {
  }
});